.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  box-shadow: 1px 1px 14px 1px skyblue;
  border-radius: 14px;
}

a {
  color: white;
  text-decoration: none;
}

b {
  color: skyblue;
  cursor: pointer;
}

a:hover {
  transition: all 1s ease-in-out;
  color: blue;
}

.App-header {
  background-color: black;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}